}
@media only screen and (max-width: 1000px) {
  .navBar {
    display: flex !important;
    height: 5vw;
  }
  .navBar .navLink {
    font-size: 1.4vw;
  }
  .loginIcon {
    margin: -0.6vw -4vw;
    width: 2.5vw;
    height: 2.2vw;
  }
  .languageImage {
    width: 3.9vw;
    margin: auto 6% auto 6%;
    padding: 9%;
  }
  .navbarItems {
    margin: 0;
  }
}
@media only screen and (max-width: 600px) {
  .navBar {
    display: flex !important;
    height: 6vw;
  }
  .suljakLogo {
    visibility: hidden;
  }
  .hamburgerIcon {
    width: 3vw;
    margin: -1vw -9vw;
    visibility: visible;
    z-index: 15;
    position: fixed;
  }
  .navBar .navLink {
    visibility: visible;
  }
  .navbar-collapse {
    margin-bottom: 20% !important;
  }
  .navBar .navLink {
    font-size: 15px;
    float: left;
    margin: 15px;
  }
  .navbarItems {
    flex-flow: column !important;
    position: fixed !important;
    padding: 50px 2% 1%;
    background-color: #2778cc;
    top: 0;
    margin: -17px !important;
    z-index: 2;
    height: 115%;
    transition: all 0.4s ease-in-out;
    flex-direction: column-reverse !important;
    justify-content: space-around !important;
  }
  .nav .navbarItems {
    margin: -17px;
  }
  .navItemsMargins {
    margin: 0 5px 0 20px;
    width: 200px;
    border-bottom: 1px solid #fbccd5;
  }
  .navLink {
    margin: 20px;
  }
  .loginIcon {
    position: relative;
    left: 68vw;
    margin: 5% 0 20% -43%;
    width: 3vw;
    height: 2.5vw;
  }
  .languageImage {
    position: relative;
    left: 67vw;
    width: 5.4vw;
    margin: 0 5% 8% -100%;
    padding: 8%;
  }
  .navbarItems {
    margin: 0;
  }
  .movingSideMenu {
    left: -100%;
  }
